@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Poppins-SemiBold.woff2") format("woff2"), url("../fonts/Poppins-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Poppins-Bold.woff2") format("woff2"), url("../fonts/Poppins-Bold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  position: relative;
  display: flex;
  margin: 0;
  min-height: 100%;
  overflow-x: hidden;
  flex-direction: column;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  background-color: #1c1b21;
}
.page__bg {
  position: fixed;
  inset: 0;
  height: 100vh;
  width: 100vw;
  background-image: url("../img/hero-bg.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #1c1b21;
  z-index: -1;
  transition: 1s;
}
.page__bg--darken {
  filter: brightness(0.3);
}
@media (min-width: 1110px) {
  .page__bg {
    background-image: url("../img/hero-bg@2x.webp");
  }
}
.page__main {
  flex-grow: 1;
}
@keyframes equaliser-animation {
  0% {
    transform: scaleY(1);
  }
  50% {
    transform: scaleY(0.1);
  }
  100% {
    transform: scaleY(1);
  }
}
.equaliser {
  position: fixed;
  inset: 0;
  width: 100vw;
  height: 100%;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #565658;
  transform: scaleY(2);
  transition: 2s;
  overflow: hidden;
}
.equaliser__line {
  display: block;
  width: 4px;
  background-color: #7a66cc;
  margin-right: 3px;
  animation-name: equaliser-animation;
  animation-duration: 0.6s;
  animation-iteration-count: infinite;
}
.equaliser__line--1 {
  height: 15px;
  animation-delay: -0.5s;
}
.equaliser__line--2 {
  height: 30px;
  animation-delay: -0.65s;
}
.equaliser__line--3 {
  height: 20px;
  animation-delay: -0.3s;
}
.equaliser__line--4 {
  height: 35px;
  animation-delay: -0.25s;
}
.equaliser__line--5 {
  height: 25px;
  animation-delay: -0.52s;
}
.equaliser__line--6 {
  height: 42px;
  animation-delay: -0.45s;
}
.equaliser__line--7 {
  height: 37px;
  animation-delay: -0.35s;
}
.equaliser__line--8 {
  height: 26px;
  animation-delay: -0.2s;
}
.equaliser__line--9 {
  height: 15px;
  animation-delay: -0.7s;
}
.equaliser__line--10 {
  height: 20px;
  animation-delay: -0.3s;
}
.equaliser__line--11 {
  height: 26px;
  animation-delay: -0.5s;
}
.equaliser__line--12 {
  height: 18px;
  animation-delay: -0.25s;
  margin-right: 0;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
.document {
  height: 100%;
  scroll-behavior: smooth;
  overflow-x: hidden;
  counter-reset: player;
}
.section-title {
  font-weight: 700;
  font-size: 48px;
  line-height: 72px;
  color: #ffffff;
}
@media (max-width: 980px) {
  .section-title {
    font-size: 1.5em;
    line-height: 42px;
  }
}
.section-title--stylized::before {
  display: inline-block;
  width: 64px;
  height: 31px;
  margin-right: 20px;
  background-image: url("../img/title-stylized.svg");
  background-size: 64px 31px;
  background-position: center;
  background-repeat: no-repeat;
  content: "";
}
.section-text {
  color: #ffffff;
  opacity: 0.7;
}
.header {
  padding: 30px 0;
  margin-bottom: 320px;
}
@media (max-width: 980px) {
  .header {
    margin-bottom: 0;
  }
}
.navigation {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (max-width: 980px) {
  .navigation {
    justify-content: space-between;
    padding: 0 30px;
    align-items: flex-start;
  }
}
.navigation__logo {
  margin-right: 65px;
}
@media (max-width: 980px) {
  .navigation__logo {
    margin-right: 0;
  }
}
.navigation__toggle {
  position: absolute;
  top: 14px;
  right: 30px;
  display: none;
}
@media (max-width: 980px) {
  .navigation__toggle {
    display: block;
  }
}
.navigation__link {
  font-size: 18px;
  line-height: 27px;
  font-weight: 600;
  text-decoration: none;
  color: #ffffff;
  transition: 0.3s;
}
.navigation__link:hover {
  color: #7a66cc;
}
.navigation__list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
  transition: 0.3s;
}
@media (max-width: 980px) {
  .navigation__list {
    width: 100%;
    transform: translate(100vw, 0);
    flex-direction: column;
    align-items: center;
    row-gap: 6vh;
  }
  .navigation__list--opened {
    transform: translate(0, 0);
  }
}
@media (max-width: 420px) {
  .navigation__list {
    row-gap: 2em;
  }
}
.navigation__item {
  margin-right: 56px;
}
@media (max-width: 980px) {
  .navigation__item {
    margin-right: 0;
  }
}
.navigation-toggle {
  background-color: transparent;
  border: none;
  height: 30px;
  padding: 0;
  cursor: pointer;
}
.navigation-toggle:hover {
  opacity: 0.6;
}
.navigation-toggle:active {
  opacity: 0.3;
}
.navigation-toggle__line {
  position: relative;
  display: block;
  width: 30px;
  height: 3px;
  border-radius: 10px 0;
  background-color: #ffffff;
}
.navigation-toggle__line::before {
  position: absolute;
  top: 10px;
  left: 0;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 10px 0;
  content: "";
}
.navigation-toggle__line::after {
  position: absolute;
  bottom: 10px;
  left: 0;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 10px 0;
  content: "";
}
.section {
  max-width: 1150px;
  width: 100%;
  padding: 0 20px;
  padding-bottom: 100px;
  margin: 0 auto;
}
.hero {
  padding: 0 20px;
  padding-bottom: 130px;
}
.hero__container {
  display: flex;
  max-width: 1110px;
  margin: 0 auto;
  flex-direction: column;
  text-transform: capitalize;
}
@media (max-width: 420px) {
  .hero__container {
    padding: 0 1em;
  }
}
.hero__single-name {
  font-weight: 700;
  font-size: 6em;
  line-height: 150px;
  color: transparent;
  -webkit-text-stroke-color: #ffffff;
  -webkit-text-stroke-width: 1px;
}
@media (max-width: 980px) {
  .hero__single-name {
    font-size: 3em;
    line-height: 100px;
  }
}
@media (max-width: 420px) {
  .hero__single-name {
    font-size: 2.5em;
    line-height: 100px;
  }
}
.hero__audio-container {
  display: grid;
  grid-template-columns: min-content auto 50px 20px 50px;
  grid-template-rows: 1fr;
  align-items: center;
}
@media (max-width: 420px) {
  .hero__audio-container {
    grid-template-columns: min-content auto;
  }
}
.logo {
  display: flex;
  align-items: center;
}
.logo__face {
  display: block;
  margin-right: 20px;
}
@media (max-width: 420px) {
  .logo__face {
    margin-right: 0;
  }
}
.logo__text {
  display: block;
}
@media (max-width: 420px) {
  .logo__text {
    display: none;
  }
}
.news-list {
  display: flex;
  max-width: 1110px;
  margin: 0 auto;
  padding: 0;
  flex-wrap: wrap;
  justify-content: flex-start;
  list-style-type: none;
}
@media (max-width: 769px) {
  .news-list {
    justify-content: center;
  }
}
.news-item {
  position: relative;
}
.news-item:not(:last-of-type) {
  margin-right: 30px;
}
@media (max-width: 980px) {
  .news-item:not(:last-of-type) {
    margin-bottom: 30px;
    margin-right: 0;
  }
}
@media (min-width: 740px) and (max-width: 1150px) {
  .news-item:not(:last-of-type) {
    margin-right: 30px;
    margin-bottom: 30px;
  }
}
.news-item__wrapper {
  max-width: 350px;
}
.news-item__wrapper:hover .news-item__text {
  max-height: 300px;
  height: 100%;
}
.news-item__image {
  display: block;
  border-radius: 20px 0;
}
@media (max-width: 420px) {
  .news-item__image {
    max-width: 300px;
    height: auto;
  }
}
.news-item__text {
  position: absolute;
  display: flex;
  align-items: center;
  max-width: 350px;
  width: 100%;
  max-height: 90px;
  height: 100%;
  bottom: 0;
  left: 0;
  margin: 0;
  padding: 2em 1em;
  padding-right: 0;
  border-radius: 20px 0;
  text-transform: capitalize;
  text-decoration: none;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  color: #ffffff;
  background-color: rgba(122, 102, 204, 0.7);
  transition: 0.3s ease;
}
@media (max-width: 420px) {
  .news-item__text {
    max-width: 300px;
    font-size: 14px;
    line-height: 20px;
  }
}
.about {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(4, 1fr);
  justify-content: space-between;
}
@media (max-width: 980px) {
  .about {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}
@media (max-width: 980px) {
  .about__title {
    grid-column: 1;
    grid-row: 1;
  }
}
.about__text-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-column: 1 / 2;
  grid-row: 2 / -1;
}
@media (max-width: 980px) {
  .about__text-wrapper {
    grid-row: 2;
    margin-bottom: 40px;
  }
}
.about__text {
  margin-bottom: 60px;
}
.about__list {
  display: grid;
  padding: 0;
  margin: 0;
  grid-template-columns: repeat(2, 1fr);
  list-style-type: none;
  row-gap: 30px;
  column-gap: 40px;
}
@media (max-width: 420px) {
  .about__list {
    grid-template-columns: 1fr;
  }
}
.about__item {
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
}
.about__item::before {
  display: block;
  width: 30px;
  height: 30px;
  margin-right: 20px;
  border-radius: 10px 0;
  background-color: #7a66cc;
  content: "";
}
.about__images-wrapper {
  display: grid;
  grid-template-columns: repeat(6, 70px);
  grid-template-rows: repeat(9, 50px);
  grid-column: -2 / -1;
  grid-row: 1 / -1;
  align-self: center;
  justify-self: end;
}
@media (max-width: 980px) {
  .about__images-wrapper {
    grid-row: 3;
    justify-self: center;
  }
}
@media (max-width: 420px) {
  .about__images-wrapper {
    grid-template-columns: repeat(6, 48px);
    grid-template-rows: repeat(9, 34px);
  }
}
.about__picture {
  display: block;
  width: max-content;
  height: max-content;
}
.about__picture--position-first {
  grid-column-start: 1;
  grid-row-start: 1;
}
.about__picture--position-second {
  grid-column-end: 3;
  grid-row-end: 3;
  z-index: -1;
}
.about__image {
  display: block;
  border-radius: 30px 0;
}
@media (max-width: 420px) {
  .about__image {
    width: 240px;
    height: auto;
  }
}
.about__image--second {
  opacity: 0.5;
}
.citate {
  position: relative;
  padding-bottom: 0;
}
@media (max-width: 980px) {
  .citate {
    padding-bottom: 200px;
  }
}
@media (max-width: 420px) {
  .citate {
    padding-bottom: 20px;
  }
}
.citate__wrapper {
  padding: 50px 80px;
  border: 1px solid #ffffff;
  border-radius: 30px 0;
  margin-bottom: 100px;
}
@media (max-width: 980px) {
  .citate__wrapper {
    padding: 50px 20px 40px;
  }
}
@media (max-width: 420px) {
  .citate__wrapper {
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
}
.citate__text {
  color: #ffffff;
  max-width: 460px;
  padding: 0;
  margin: 0;
  margin-bottom: 25px;
}
.citate__text::after {
  display: block;
  width: 100%;
  height: 1px;
  margin-top: 25px;
  background-color: #ffffff;
  content: "";
}
@media (max-width: 980px) {
  .citate__text {
    max-width: unset;
  }
}
.citate__author {
  text-decoration: none;
  color: #7a66cc;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
}
.citate__image {
  position: absolute;
  right: 80px;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  border-radius: 30px 0;
}
@media (max-width: 980px) {
  .citate__image {
    width: 320px;
    height: auto;
    right: 50%;
    top: 0;
    transform: translate(50%, 50%);
  }
}
@media (max-width: 420px) {
  .citate__image {
    display: none;
  }
}
.tracks {
  display: grid;
  padding-top: 80px;
  grid-template-columns: min-content auto;
  grid-template-rows: repeat(4, min-content);
  column-gap: 80px;
}
@media (max-width: 980px) {
  .tracks {
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, min-content);
  }
}
.tracks__container {
  display: grid;
  grid-template-columns: min-content auto 50px 20px 50px;
  align-items: center;
  margin: 30px 0;
}
@media (max-width: 420px) {
  .tracks__container {
    grid-template-columns: min-content;
  }
}
.tracks__title {
  padding: 0;
  margin: 0;
  grid-column: 2;
}
@media (max-width: 980px) {
  .tracks__title {
    margin: 30px 0;
    grid-column: 1;
  }
}
.tracks__image-wrapper {
  grid-row: 1 / -1;
  padding: 0;
  margin: 0;
  border: 18px solid rgba(122, 102, 204, 0.3);
  border-radius: 50%;
  aspect-ratio: 1;
}
@media (max-width: 980px) {
  .tracks__image-wrapper {
    grid-row: 1;
    max-width: 436px;
    justify-self: center;
  }
}
@media (max-width: 420px) {
  .tracks__image-wrapper {
    grid-row: 1;
    max-width: 296px;
    border-width: 9px;
  }
}
.tracks__picture {
  display: block;
  border: 20px solid rgba(122, 102, 204, 0.6);
  border-radius: 50%;
  z-index: -10;
}
@media (max-width: 980px) {
  .tracks__picture {
    width: 400px;
    height: auto;
  }
}
@media (max-width: 420px) {
  .tracks__picture {
    width: 260px;
    height: auto;
    border-width: 10px;
  }
}
.tracks__list {
  padding: 0;
  list-style-type: none;
  grid-column: 2;
}
@media (max-width: 980px) {
  .tracks__list {
    grid-column: 1;
  }
}
.tracks__item {
  display: grid;
  grid-template-columns: 40px auto;
  color: #ffffff;
  cursor: pointer;
}
.tracks__item::before {
  counter-increment: player;
  content: counters(player, "", decimal-leading-zero);
  opacity: 0.5;
  color: #ffffff;
  font-weight: initial;
}
.tracks__item:not(:last-of-type) {
  margin-bottom: 20px;
}
.tracks__track-item--active {
  font-weight: 600;
  color: #7a66cc;
}
.player__cur-time-line {
  display: block;
  margin-right: 45px;
  height: 2px;
  appearance: none;
  background-image: linear-gradient(to right, #7a66cc 100%, #ffffff);
  background-size: 0% 100%;
  background-repeat: no-repeat;
  cursor: pointer;
}
@media (max-width: 420px) {
  .player__cur-time-line {
    margin-right: 0;
  }
}
.player__cur-time-line::-webkit-slider-thumb {
  appearance: none;
  width: 2px;
  height: 20px;
  background: #7a66cc;
  cursor: pointer;
}
.player__time {
  color: #ffffff;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
.player__btn-play {
  width: 25px;
  height: 30px;
  padding: 0;
  margin-right: 45px;
  background-color: transparent;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  border-right: none;
  border-left: 25px solid #ffffff;
  cursor: pointer;
}
@media (max-width: 420px) {
  .player__btn-play {
    margin-right: 20px;
  }
}
.player__btn-play--pause {
  border: none;
  border-radius: 0;
  background: linear-gradient(to right, #ffffff 30%, transparent 30% 70%, #ffffff 70% 100%);
}
@media (max-width: 420px) {
  .player__play-time {
    grid-row: 2;
    grid-column: 2;
    justify-self: start;
  }
  .player__between-time {
    display: none;
  }
  .player__full-time {
    grid-row: 2;
    grid-column: 2;
    justify-self: end;
  }
}
.gallery__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 30px;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.gallery__item--small {
  grid-row: 1;
}
.gallery__item--big-vertical {
  grid-row: 1 / -1;
}
.gallery__item--big-horizontal {
  position: relative;
  grid-row: 2;
  grid-column: 2 / 4;
}
.gallery__item-text {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 0;
  margin: 0;
  transform: translate(-50%, -50%);
  white-space: nowrap;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: #7a66cc;
}
.gallery__image {
  display: block;
  border-radius: 30px 0;
}
.tours {
  position: relative;
  padding-bottom: 0;
}
.tours__track {
  position: relative;
}
.tours__buttons-wrapper {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  display: flex;
  width: 100%;
  justify-content: space-between;
  z-index: 10;
}
@media (min-width: 900px) {
  .tours__buttons-wrapper {
    display: none;
  }
}
.tours__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 21px 10px;
  background: none;
  border: 1px solid #ffffff;
  border-radius: 10px 0;
  opacity: 0.5;
  cursor: pointer;
}
.tours__button--disabled {
  opacity: 0.2;
  cursor: default;
  touch-action: none;
  pointer-events: none;
}
.tours__button--prev {
  left: -40px;
}
@media (max-width: 980px) {
  .tours__button--prev {
    left: 40px;
  }
}
@media (max-width: 420px) {
  .tours__button--prev {
    left: -10px;
  }
}
.tours__button--forward {
  right: -40px;
}
@media (max-width: 980px) {
  .tours__button--forward {
    right: 40px;
  }
}
@media (max-width: 420px) {
  .tours__button--forward {
    right: -10px;
  }
}
.tours__button-icon--forward {
  transform: rotate(180deg);
}
.tours__list {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  gap: 30px;
  list-style-type: none;
  padding: 0;
  margin: 0;
  margin-bottom: 100px;
  transition: 1.3s;
}
@media (min-width: 900px) {
  .tours__list {
    flex-wrap: nowrap;
  }
}
@media (max-width: 420px) {
  .tours__list {
    margin-left: -45px;
  }
}
.tours__card {
  transition: 0.3s;
}
@media (max-width: 900px) {
  .tours__card {
    opacity: 0;
  }
}
.tours-card {
  display: grid;
  border-radius: 30px 0;
  max-width: 350px;
  background-color: #2f2b2b;
}
.tours-card__image {
  border-radius: 30px 0;
}
@media (max-width: 420px) {
  .tours-card__image {
    width: 240px;
    height: auto;
  }
}
.tours-card__text-wrapper {
  padding-left: 35px;
}
.tours-card__city {
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
  opacity: 0.3;
}
@media (max-width: 420px) {
  .tours-card__city {
    font-size: 14px;
    line-height: 22px;
  }
}
.tours-card__club {
  color: #ffffff;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 420px) {
  .tours-card__club {
    font-size: 14px;
    line-height: 22px;
  }
}
.tours-card__date {
  font-weight: 700;
  color: #ffffff;
  opacity: 0.5;
  text-transform: uppercase;
}
.tours-card__button {
  padding: 16px 55px;
  border-radius: 30px 0;
  background-color: #7a66cc;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  font-weight: 700;
  justify-self: end;
}
@media (max-width: 420px) {
  .tours-card__button {
    padding: 10px 30px;
  }
}
.footer {
  display: flex;
  padding: 40px 0;
  gap: 60px;
  flex-direction: column;
  align-items: center;
}
.footer__logo-face {
  margin-right: 20px;
}
.footer__logo-text {
  display: block;
}
.footer__contacts-list {
  display: flex;
  padding: 0;
  margin: 0;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
  text-transform: uppercase;
}
@media (max-width: 420px) {
  .footer__contacts-list {
    gap: 30px;
  }
}
.footer__contacts-item {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  color: #ffffff;
}
@media (max-width: 420px) {
  .footer__contacts-item {
    grid-template-columns: 1fr;
    gap: 0;
  }
}
.footer__contacts-name {
  padding: 0;
  margin: 0;
  text-align: right;
  justify-self: flex-end;
}
@media (max-width: 420px) {
  .footer__contacts-name {
    justify-self: center;
  }
}
.footer__contacts-value {
  padding: 0;
  margin: 0;
  text-align: left;
  text-decoration: none;
  color: #ffffff;
}
@media (max-width: 420px) {
  .footer__contacts-value {
    text-align: center;
  }
}
.footer__social-list {
  display: flex;
  gap: 30px;
  margin: 0;
  padding: 0;
  flex-wrap: wrap;
  justify-content: center;
  list-style-type: none;
}
@media (max-width: 980px) {
  .footer__social-list {
    max-width: 300px;
  }
}
.footer__social-item {
  justify-self: flex-start;
}
.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  background-color: #7a66cc;
  border: 8px solid #2c2c2c;
  border-radius: 50%;
}
.footer__social-icon {
  text-align: center;
  vertical-align: middle;
}
.footer__copyright {
  color: #ffffff;
  opacity: 0.5;
}
